.about{
    background-color: var(--container-color);
    transition: background-color .4s;
}

.about__container{
    row-gap: 0;
}

.about__perfil{
    position: relative;
    justify-self: center;
    margin-block: 2.5rem 4rem;
}

.about__image{
    width: 220px;
}

.about__img{
    position: relative;
    border: 4px solid var(--white-color);
    z-index: 1;
}

.about__shadow,
.about__line,
.about__box{
    position: absolute;
}

.about__shadow{
    width: 150px;
    height: 385px;
    background-color: var(--body-color);
    top: -2.5rem;
    right: -3.5rem;
    border-bottom: 4px solid var(--first-color);
    transition: background-color .4s;
}

.about__perfil .geometric-box{
    top: 1.5rem;
    right: -2.8rem;
}

.about__line{
    filter: invert(1);
    right: -1.25rem;
    top: 6rem;
    width: 50px;
    transition: filter .4s;
}

.about__box{
    width: 40px;
    height: 40px;
    background-color: var(--first-color);
    right: -.5rem;
    bottom: 1.5rem;
}

.about__info{
    padding-left: 1.25rem;
}

.about__description{
    position: relative;
    color: var(--title-color);
    margin-bottom: 1.5rem;
}

.about__description::after{
    content: '';
    width: 20px;
    height: 1px;
    background-color: var(--title-color);
    position: absolute;
    left: -1.75rem;
    top: .5rem;
}

.about__list{
    list-style: square;
    color: var(--title-color);
    margin-bottom: 3rem;
}

.about__buttons{
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 1rem;
}

/* For large devices */
@media screen and (min-width: 1150px){

    .about__container{
        grid-template-columns: 440px 525px;
        gap: 1rem 9rem;
        align-items: center;
        padding-block: 1rem;
    }
    .about__perfil{
        order: -1;
        grid-row: 1/3;
    }
    .about__image{
        width: 350px;
    }
    .about__img{
        border: 8px solid var(--white-color);
    }
    .about__shadow{
        width: 240px;
        height: 615px;
        top: -4rem;
        right: -5.5rem;
        border-bottom: 6px solid var(--first-color);
    }
    .about__perfil .geometric-box{
        right: -4rem;
        top: 4.5rem;
    }
    .about__line{
        width: 80px;
        right: -2rem;
        top: 10rem;
    }
    .about__box{
        width: 64px;
        height: 64px;
        right: -.75rem;
    }
    .about__container .section__title-1{
        align-self: flex-end;
        margin-inline: 0;
    }
    .about__info{
        align-self: flex-start;
        padding-left: 3rem;
    }
    .about__description{
        font-size: var(--h2-font-size);
        margin-bottom: 2rem;
    }
    .about__description::after{
        width: 32px;
        height: 2px;
        left: -3rem;
        top: 14px;
    }
    .about__list{
        margin-bottom: 5rem;
    }
    .about__buttons{
        justify-content: initial;
    }
  }